import React from "react";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react";

import HondaImg from "../assets/honda.svg";
import DentsuImg from "../assets/dentsu.svg";
import KelloggsImg from "../assets/kelloggs.svg";
import ResonanceImg from "../assets/resonance.svg";

const MidBannerOne = ({ className }) => {
  return (
    <section className={className}>
      <div className="content">
        <h4>
          {"EasySocial is the trusted marketing partner"}
          <br />
          {"for prominent global brands."}
        </h4>
        <div className="slider-container">
          <Swiper
            spaceBetween={0}
            slidesPerView={1.6}
            loop={true}
            breakpoints={{
              585: {
                slidesPerView: 2.5,
              },
              768: {
                slidesPerView: 3,
              },
              960: {
                slidesPerView: 4,
              },
            }}
          >
            <SwiperSlide>
              <img src={HondaImg} alt="Honda" className="slide-image" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={DentsuImg} alt="Dentsu" className="slide-image" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={KelloggsImg} alt="Kelloggs" className="slide-image" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={ResonanceImg} alt="Resonance" className="slide-image" />
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </section>
  );
};

export default MidBannerOne;
